___CSS_LOADER_EXPORT___.push([module.id, ".fade-enter-active[data-v-2f9964c2],.fade-leave-active[data-v-2f9964c2]{transition:opacity .2s ease-in-out}.fade-enter-from[data-v-2f9964c2],.fade-leave-to[data-v-2f9964c2]{opacity:0}", "",{"version":3,"sources":["webpack://./resources/js/components/users/Clock.vue"],"names":[],"mappings":"AAuGA,wEAEA,kCACA,CAEA,kEAEA,SACA","sourcesContent":["<template>\n    <div class=\"p-2\">\n        <div class=\"d-flex flex-column flex-md-row align-items-md-center\">\n            <div class=\"text-center\">\n                <label for=\"random_number\">\n                    <h6 class=\"fw-bold d-flex flex-row align-items-center\">\n                        <i class=\"fa-duotone fa-hashtag-lock fa-2x\"></i>\n                        User Random Number\n                    </h6>\n                </label>\n                <vs-input\n                    id=\"random_number\"\n                    autofocus\n                    block\n                    name=\"random_number\"\n                    maxlength=\"3\"\n                    type=\"text\"\n                    list=\"users-list\"\n                    v-model.trim=\"search\"\n                ></vs-input>\n                <datalist id=\"users-list\">\n                    <option\n                        v-for=\"user in allUsers\"\n                        :key=\"user.id\"\n                        :value=\"user.user_number\"\n                    >\n                        {{ user.name }}\n                    </option>\n                </datalist>\n                <transition name=\"fade\" mode=\"in-out\">\n                    <h6 v-if=\"notFound\" class=\"text-center text-danger fw-bold my-2\">\n                        Not found\n                    </h6>\n                </transition>\n                <vs-button\n                    block\n                    class=\"mt-3\"\n                    flat\n                    dark\n                    :disabled=\"search === ''\"\n                    @click=\"findUser()\"\n                    :loading=\"loading\"\n                >\n                    <h6 class=\"m-0 fw-bold\">\n                        <i class=\"fa-duotone fa-magnifying-glass\"></i>\n                        Find\n                    </h6>\n                </vs-button>\n            </div>\n\n            <div class=\"flex-grow-1 m-3\">\n                <transition name=\"fade\" mode=\"in-out\">\n                    <router-view :key=\"$route.params.slug\"></router-view>\n                </transition>\n            </div>\n        </div>\n    </div>\n</template>\n<script>\nimport {mapGetters} from \"vuex\";\n\nexport default {\n    data(){\n        return {\n            search: '',\n            loading: false,\n            notFound: false\n        }\n    },\n    computed:{\n        ...mapGetters(['allUsers', 'allShifts'])\n    },\n    methods:{\n        findUser(){\n            this.loading = true;\n            let user = this.allUsers.find(user => user.user_number.toLowerCase() === this.search.toLowerCase());\n            if(user === undefined){\n                this.notFound = true;\n            }else{\n                if(!this.$route.path.includes(user.id)){\n                    this.notFound = false;\n                    this.$router.push({ name: 'clockUser', params: { slug: user.id }})\n                }\n            }\n            this.loading = false\n        },\n        openNotification(title, text) {\n            const notification = this.$vs.notification({\n                title,\n                text\n            });\n        }\n    },\n    watch:{\n        search(value){\n            if(value === ''){\n                this.$router.push({ name: 'clock' })\n            }\n        }\n    }\n}\n</script>\n<style scoped>\n    .fade-enter-active,\n    .fade-leave-active {\n        transition: opacity 0.2s ease-in-out;\n    }\n\n    .fade-enter-from,\n    .fade-leave-to {\n        opacity: 0;\n    }\n</style>\n"],"sourceRoot":""}]);
